var that;
export default {
	data() {
		return {
			page: 1,
			list: [
				{
					stuats: '报名成功',
					time: '2020-02-07 14:56:12',
					ctime: '你参加的XXX会务已成功报名，请查看>>'
				},
				{
					stuats: '报名成功',
					time: '2020-02-07 14:56:12',
					ctime: '你参加的XXX会务已成功报名，请查看>>'
				}
			],
			onlist: false,
			nomore: false,
			access_token: ''
		};
	},
	methods: {
		login() {
			that.$zax.yzlogin();
		},
		getlist() {
			if (that.nomore) {
				return;
			}
			that.onlist = false;
			this.$request
				.request({
					url: that.api.live.msg,
					data: {
						type: 7,
						page: that.page
					},
					Toast: '正在加载'
				})
				.then(res => {
					var list = res;
					if (list.length > 0) {
						for (let i in list) {
							let time = that.$zax.formatDate(list[i].createtime * 1000);
							console.log(time);
							list[i].creatText = time.year + '-' + time.month + '-' + time.second + ' ' + time.minute + ':' + time.hour + ':' + time.second;
						}
						console.log(list);
						(that.list = that.list.concat(list)), (that.page = that.page + 1);
					} else {
						if (that.page != 1) {
							that.nomore = true;
						} else {
							that.onlist = true;
						}
					}
				});
		},
		tpinfo(e) {
			uni.navigateTo({
				url: '/pages/liveBroadcast/livePLayer/player/player?id=' + e.currentTarget.dataset.id
			});
		}
	},
	onLoad() {
		that = this;
		// that.getlist();
	},
	onShow() {
		that.access_token = uni.getStorageSync('access_token');
	},
	onPullDownRefresh() {
		that.getlist();
	}
};
